{
padding :0;
margin :0;
color:white;

}

.HeaderBody{
    font-size: 17px;
    background-color: #15384C;
    height: 70px;
    width: 100%;
    color: white;
    top: 0;

    z-index: 999;
    position: fixed;
}
.navBar .alone{
    text-decoration: none;
    color: white;
}

.navBarMain li{

    list-style-type: none;
}

.navBar{
    margin: 0 80px 0 40px;
    margin-top: 5px;
    display: flex;
    text-align: center;
    justify-content: space-around;
    transition: 0.3s;
    height: 70px;

}
.navBarMain Link,
.navBarMain test Link{
    text-decoration: none;
}
.navBarMain {
    height: auto;

    transition: 0.1s;
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: auto;


}

.navBarMain .alone{
    padding: 0 10px;
    position: relative;
    white-space: nowrap;
    cursor: pointer;


}
.navBar .alone:hover{
    color: #20BEC6;

    transition: 0.3s linear;

}

.navBarMain .more{
    position: absolute;
    transform: translateX(-60px);
}
.navBarMain .moreList .test li{
   margin-top: 20px;


}
.navBarMain .moreList .test{

    border-radius: 8px;
    background-color: #15384C;
    background: rgba(21,56,76,0.7);
    width: 130px;
    padding: 20px;
    padding-right: 10px;
    text-align: left;
display: none;
}
.symbol{

}
.navBarMain .moreList:hover .test{
display: block;
}
.navBarMain .alone::before{

    content: "";
    position: absolute;
    width: 3px;
    height: 2px;
z-index: 999;
    background-color : #20BEC6;
    transition: 0.3s;
    margin-right: 25px;
    left: 10%;
    top: 35px;
    visibility: hidden;

}




.navBarMain .alone:hover::before{
    width: 80%;
    visibility: visible;
}
.navBarOther{

    display: flex;
   width: 200px;
text-align: center;
    justify-content: space-around;

}
.navBarOther a{
width: 5px;
    white-space: nowrap;
    height: 20px;
margin-top: 18px;
}

.logo{
    flex-wrap: wrap;
    z-index: 999;
    top: 0;
    transition: 0.3s;
margin-bottom: 18px;
    min-width: 150px;
    height: auto;

    width: 170px;
    height: 112px;
    transform: translateY(-25px);

cursor: pointer;

}

@media screen and (max-width: 880px) {
    .navBarMain{
        width: 10px;
        visibility: hidden;


    }

}
@media screen and (max-width: 1000px) {
    .navBarMain  li:nth-child(5) {
        display: none;

    }}
    @media screen and (max-width: 1000px) {
    .navBarMain li:nth-child(7) {
        display: none;

    }}

  