.main{
    margin: 80px 80px 0 80px;
color: white;
    font-size: 17px;
width: auto;

}
.image {
   width: 60%;
    height: 50%;

}
.describe{
    border: 1px red solid;
    display: flex;
    text-align: center;
    justify-content: space-between;

}
.firstColumn{
    text-align: left;
}
.firstColumn li{
    padding: 16px;
    height: auto;
}
.secondColumn li{
    padding: 16px;
height: auto;

}
.secondColumn{
    text-align: left;


}
.info{

overflow: hidden;
    display: flex;
    justify-content: space-between;
    text-align: center;
    border: 1px red solid;
    width: 40%;


}
.info li{
    width: 20%;

    list-style-type: none;

}